.faq {
  margin-bottom: 135px;
  padding-top: 55px;
}

.faq__title {
  font-size: 44px;
  margin: 0;
  padding: 70px 0;
  color: $prime-color;
  background: url('../img/faq.png') no-repeat top left/100% auto;
}

.faq__list {
  padding: 0;
  list-style: none;
}

.faq__item-title {
  font-weight: bold;
  font-size: 24px;
  margin: 0;
}

.faq__link {
  width: 100%;
  display: inline-block;
  padding: 15px 200px 15px 25px;
  cursor: pointer;
  opacity: 0.7;
  background: #F2F2F2 url("../img/lupa.png") no-repeat center right 25px;

  &.open {
    opacity: 1;
    background-image: url('../img/close.png');
  }
}

.faq__item {
  margin-bottom: 28px;
}

.faq__detail {
  display: none;
  margin: 20px 25px 0;
  max-width: 495px;

  & a {
    color: $prime-color;
    text-decoration: underline;

    &:hover,
    &:focus,
    &:active {
      text-decoration: none;
    }
  }
}

@media (max-width: $mobile-width) {
  .faq {
    margin-bottom: 40px;
    padding-top: 0;
  }

  .faq__list {
    margin: 0;
  }

  .faq__link {
    padding: 5px 47px 5px 5px;
    opacity: 1;
  }

  .faq__title {
    font-size: 24px;
    padding: 35px 0 5px;
  }

  .faq__detail {
    margin: 23px 0 0;
  }

  .faq__item {
    margin-bottom: 15px;
  }

  .faq__item-title {
    font-size: 15px;
  }
}
