.services .wrapper {
  padding-top: 60px;
  padding-bottom: 75px;
  background: url('../img/girl.jpg') no-repeat top 60px right/40% auto,
              url('../img/news-bg.png') no-repeat top 60px left -50px/80% auto;
}

.services__wrap {
  max-width: 50%;
  width: 500px;
}

.services__text {
  margin-bottom: 50px;
}

.services__form {
  margin-bottom: 220px;
}

.services__form .button {
  padding: 13px;
  font-size: 22px;
  border: none;
}

.services__bottom {
  color: $prime-color;
  text-align: center;
  padding-bottom: 15px;
  background: url('../img/arrow-bottom.png') no-repeat bottom center;
}

.services__list {
  margin-bottom: 100px;
}

.services__item {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  & > div {
    width: 48%;
  }
}

.services__item--one {
  margin-bottom: 40px;

  h3 {
    font-size: 24px;
    text-transform: inherit;
    margin: 40px 0 15px;
  }
  & ol {
    padding-left: 0;
    list-style-position: inside;
  }
}

.number {
  position: relative;
  padding-left: 80px;

  & .subtitle {
    text-transform: inherit;
    font-size: 38px;
  }

  &::before {
    font-size: 220px;
    font-family: $title-font;
    color: rgba(#000, 0.5);
    position: absolute;
    left: 0;
  }

  &.number-one::before {
    content: "1";
    top: -57px;
  }

  &.number-two::before {
    content: "2";
    top: 72px;
    left: -18px;
  }
}

.number-two {
  padding-top: 100px;
  background: url('../img/bezopasno.png') no-repeat top center/100% auto;
}

.main {
  text-align: center;
  font-family: $title-font;
  font-size: 24px;
  padding: 15px 0;
  background: url('../img/nadejno.png') no-repeat center/100% auto;

  & .accent {
    color: $prime-color;
  }
}

.services__item--two img {
  position: relative;
  z-index: -1;
}

@media (max-width: $mobile-width) {
  .services__wrap {
    max-width: 100%;
    margin: 0 auto;
  }

  .services .wrapper {
    margin-top: 15px;
    padding-top: 300px;
    padding-bottom: 10px;
    background-size: auto 280px, 100% auto;
    background-position: top center, top 250px center;
  }

  .services__form {
    margin-bottom: 0;
  }

  .services__bottom {
    display: none;
  }

  .services__item {
    flex-wrap: wrap;
  }

  .services__item > div {
    width: 100%;
  }

  .services__item--one .two {
    order: -1;
  }

  .number .subtitle {
    font-size: 24px;
  }

  .main {
    font-size: 14px;
  }

  .number {
    padding-left: 55px;

    &::before {
      font-size: 180px;
    }
  }

  .number.number-two {
    padding-top: 70px;

    &::before {
      top: 40px;
    }

    & ~ .two {
      margin-bottom: -325px;
    }
  }
}
