.owner-form .wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding-left: 140px;
  padding-right: 105px;
  max-width: 100%;
  align-items: flex-start;
}

.owner-form__proposion {
  width: 60%;
  background-color: $prime-color;
  padding: 60px 70px;
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.25);

  & h1 {
    font-size: 44px;
    max-width: 755px;
    padding-bottom: 310px;
    margin: 0 0 20px;
    background: url('../img/owner.png') no-repeat bottom right/auto 345px;
  }

  & h2 {
    font-size: 26px;
    margin: 23px 0;
  }
}

.owner-form__callback {
  padding: 60px 80px;
  width: 40%;

  & h3 {
    font-size: 24px;
    text-transform: inherit;
    margin: 0 0 25px;
  }

  & .form__input {
    background: rgba(0, 0, 0, 0.13);
    backdrop-filter: blur(30px);
    color: rgba($text-color, 0.8);
  }

  & textarea {
    width: 100%;
    border: none;
    padding: 14px 50px 14px 10px;
    margin-bottom: 30px;
    background: rgba(0, 0, 0, 0.13) url('../img/write.png') no-repeat top 15px right 15px;
  }

  & .form__input::placeholder,
  & textarea::placeholder {
    color: rgba(#000, 0.36);
  }

  & .form__file-label  {
    background-image: url('../img/file-icon.png');
    background-repeat: no-repeat;
    background-position: center left 13px;
    margin-bottom: 35px;
    position: relative;
  }

  & .file {
    position: absolute;
    width: 100%;
    left: 0;
    opacity: 0;
  }

  & .lze-capcha {
    text-align: center;
    margin-bottom: 30px;
  }
}

input[type="radio"]:checked,
input[type="radio"]:not(:checked) {
  width: 92px;
  height: 26px;
  opacity: 0;
  position: absolute;
  z-index: 1;
  cursor: pointer;
}

input[type="radio"]:checked + label,
input[type="radio"]:not(:checked) + label {
  display: inline-block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  margin-bottom: 35px;
  margin-right: 20px;
  line-height: 26px;
}

input[type="radio"]:checked + label:before,
input[type="radio"]:not(:checked) + label:before {
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  width: 26px;
  height: 26px;
  border: 3px solid $prime-color;
}

input[type="radio"]:checked + label:after {
  content: "";
  position: absolute;
  left: 7px;
  top: 7px;
  width: 12px;
  height: 12px;
  background-color: $prime-color;
}

@media (max-width: 1100px) {
  .owner-form .wrapper {
    padding: 0;
  }

  .owner-form__proposion,
  .owner-form__callback {
    min-width: 560px;
  }
}

@media (max-width: $mobile-width) {
  .owner-form .wrapper {
    padding: 0;
  }

  .owner-form__proposion,
  .owner-form__callback {
    min-width: 100%;
    width: 100%;
    padding: 30px 20px;
  }
}
