.button {
  display: inline-block;
  padding: 15px 25px;
  width: 100%;
  text-decoration: none;
  text-transform: uppercase;
  font-family: $title-font;
  transition: 0.3s;
  background-color: $prime-color;
  font-size: 54px;
  color: $text-color;
  text-align: center;

  &:hover,
  &:focus,
  &:active {
    box-shadow: 8px 20px 40px rgba(138, 100, 11, 0.3);
  }
}

.button--dark {
  background-color: $text-color;
  color: #fff;

  &:hover,
  &:focus,
  &:active {
    box-shadow: 8px 20px 40px rgba(#000, 0.3);
  }
}

.button--news {
  text-align: left;
  padding: 5px 60px 5px 10px;
  position: relative;

  &::after {
    content: "";
    width: 32px;
    height: 32px;
    background: #212121;
    position: absolute;
    top: 50%;
    right: 18px;
    margin-top: -16px;
  }
}

.button--form {
  border: none;
  font-size: 22px;
  padding: 13px;
  color: #fff;
  background: $text-color url('../img/button-phone.png') no-repeat center left 13px;

  &:hover,
  &:focus,
  &:active {
    background-color: rgba($text-color, 0.6);
    backdrop-filter: blur(30px);
  }
}

.button--owner-form {
  border: none;
  font-size: 22px;
  padding: 13px;
}

.button--non-page {
  font-size: 22px;
  padding: 13px;
  max-width: 340px;
}

.button--load {
  background: #F2F2F2;
  color: #A7A7A7;
  position: relative;
  margin-top: 30px;

  &:hover,
  &:focus,
  &:active {
    background-color: rgba($text-color, 0.8);
    color: #fff;
    box-shadow: none;
  }

  &::before,
  &::after {
    width: 100px;
    transform: scaleY(1.45);
    text-align: center;
    background: inherit;
    color: inherit;
    display: inline-block;
    position: absolute;
    top: 15px;
  }

  &::before {
    content: "<";
    left: 0;
    border-right: 12px solid #fff;
  }

  &::after {
    content: ">";
    right: 0;
    border-left: 12px solid #fff;
  }
}

@media (max-width: $mobile-width) {
  .button {
    padding: 8px 25px;
    font-size: 28px;
  }

  .button--load {
    &::before,
    &::after {
      display: none;
    }
  }
}
