.contacts-page .wrapper {
  background: #000 url('../img/contacts.jpg') no-repeat center/cover;
  max-width: 100%;
  padding: 67px 172px 5px 209px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  color: #fff;

  & h1 {
    margin: 0 0 20px;
    font-size: 44px;
    width: 100%;
  }

  & .title {
    font-size: 12px;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.42);
    opacity: 0.7;
  }
}

.contacts__social .social__icon {
  fill: #fff;
  opacity: 0.5;

  &:hover,
  &:focus,
  &:active {
    opacity: 1;
  }
}

.other-fill {
  fill: #000 !important;
}

.contacts-page__form {
  max-width: 340px;

  & h2 {
    font-size: 24px;
    margin: 0 0 20px;
  }

  & p {
    color: rgba(255, 255, 255, 0.54);
    margin-bottom: 20px;
  }
}

.form-contacts {
  margin-bottom: 25px;

  & .form__input {
    background: rgba(197, 197, 197, 0.13);
  }

  & textarea.form__input {
    height: auto;
    background: rgba(197, 197, 197, 0.13) url('../img/write-gray.png') no-repeat top 10px right 10px;
  }
}

.contacts-page__items {
  padding-top: 10px;
  width: 205px;
  max-width: 30%;

  & h3 {
    font-size: 24px;
    text-transform: inherit;
    font-weight: normal;
    margin: 0 0 25px;
  }
}

.contacts-page__item {
  margin-bottom: 70px;

  .title {
    margin-bottom: 5px;
  }

  .phone,
  .email,
  .value {
    font-family: $title-font;
    font-size: 23px;
    color: #fff;
    margin-bottom: 16px;

    & span {
      color: $prime-color;
    }
  }
}

.contacts-page__adres {
  width: 100%;
  text-align: center;
  padding-top: 40px;
  margin-top: -55px;
  background: url('../img/location.png') no-repeat top center;

  & .value {
    font-weight: 700;
  }
}

.contacts__map img {
  width: 100%;
  margin-bottom: -4px;
}

.contacts__map .mob {
  display: none;
}

@media (max-width: $mobile-width) {
  .contacts-page .wrapper {
    padding: 20px 20px;
    position: relative;
    background-image: url('../img/contacts-mob.jpg');

    & h1 {
      order: -2;
      text-align: center;
      font-size: 28px;
    }
  }

  .contacts-page__form {
    text-align: center;
    margin: 0 auto;
  }

  .contacts-page__items {
    padding-top: 0;
    max-width: 100%;
    width: 100%;
    order: -1;
  }

  .contacts-page__item .mob {
    display: none;
  }

  .contacts__social {
    position: absolute;
    top: 75px;
    right: 20px;
    display: flex;
    flex-direction: column;
  }

  .contacts-page__time-work {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-bottom: 50px;

    & h3 {
      width: 100%;
      text-align: center;
    }
  }

  .contacts-page__adres {
    display: none;
  }

  .contacts__map .desc {
    display: none;
  }

  .contacts__map .mob {
    display: inline-block;
  }
}
