.new .wrapper {
  position: relative;
  padding-top: 210px;
  background: url('../img/news-name.png') no-repeat top left/60% auto;
}

.new__back {
  position: absolute;
  top: 4%;
  right: 0;
  font-size: 0;
  width:261px;
  height: 88px;
  display: inline-block;
  background: url('../img/back.png') no-repeat center/contain;
 }

.new__img {
  float: right;
  margin-left: 30px;
  max-width: 70%;
  width: 775px;
}

.new__title {
  text-transform: inherit;
  font-size: 44px;
  margin-bottom: 50px;
}

.new__data {
  margin-bottom: 50px;

  & .title {
    font-weight: 500;
    font-size: 12px;
    opacity: 0.7;
    margin-bottom: 8px;
  }

  & .value {
    font-family: $title-font;
    font-size: 22px;
    margin-bottom: 0;
    font-weight: bold;
  }
}

.new__subtitle {
  clear: both;
  font-size: 22px;
  text-transform: inherit;
  margin-bottom: 30px;
}

@media (max-width: $mobile-width) {
  .new .wrapper {
    padding-top: 110px;
    background-size: 100% auto;
    display: flex;
    flex-direction: column;
  }

  .new__img {
    float: none;
    max-width: 100%;
    margin-left: 0;
    margin-bottom: 25px;
  }

  .new__title {
    order: -1;
    margin-bottom: 25px;
    font-size: 28px;
  }

  .new__back {
    width: 162px;
    height: 52px;
    top: 30px;
  }

  .new__data {
    margin-bottom: 0;
  }
}
