.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
}

.pagination__title {
  font-weight: 500;
  font-size: 12px;
  margin-right: 10px;
  opacity: 0.7;
}

.pagination__list {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
  font-size: 22px;
  font-family: $title-font;
}

.pagination__item {
  margin-right: 5px;
  cursor: pointer;

  & a {
    color: $text-color;
  }

  &:hover a,
  &:focus a,
  &:active a,
  &.active a {
    color: $prime-color;
    text-decoration: underline;
  }
}

@media (max-width: $mobile-width) {
  .pagination {
    margin-top: 10px;
  }
}
