.vacancies .wrapper {
  padding-top: 215px;
  background: url('../img/vacancies.png') no-repeat top 50px center/100% auto;
}

.vacancies__item {
  width: 490px;
  position: relative;
  max-width: 50%;
  margin-bottom: 165px;

  &::after {
    content: "";
    width: 450px;
    height: 604px;
    position: absolute;
    top: -150px;
    left: 155%;
    background-size: contain;
    background-position: top center;
    background-repeat: no-repeat;
  }

  &:nth-of-type(even) {
    margin-left: auto;

    &::after {
      right: 155%;
      left: auto;
    }
  }

  &:nth-of-type(1)::after {
    background-image: url('../img/vac-1.png');
  }

  &:nth-of-type(2)::after {
    background-image: url('../img/vac-2.png');
  }

  &:nth-of-type(3)::after {
    background-image: url('../img/vac-3.png');
  }

  &:nth-of-type(4)::after {
    background-image: url('../img/vac-4.png');
  }
}

.title-form {
  font-size: 44px;
  margin-bottom: 20px;
  text-align: center;
}

.discr-form {
  font-weight: 500;
  margin-bottom: 55px;
  text-align: center;
}

.form-vacancies {
  width: 700px;
  max-width: 100%;
  margin: 0 auto 150px;
  padding: 0;
  position: relative;

  & .form__label  {
    position: absolute;
    top: -20px;
    left: 0;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    color: rgba(0, 0, 0, 0.38);
  }
}

.form__fh {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;

  & > * {
    width: 49%;
  }
}

.form__select {
  color: rgba(0, 0, 0, 0.36);
  position: relative;

  &::after {

  }
}

@media (max-width: 1430px) {
  .vacancies__item::after {
    width: 350px;
    left: 100%;
    top: -70px;
  }

  .vacancies__item:nth-of-type(even)::after {
    right: 100%;
  }
}

@media (max-width: 710px) {
  .vacancies .wrapper {
    padding-top: 50px;
    background: url('../img/vacancies-mob.png') no-repeat top center/100% auto;
  }

  .vacancies__item {
    padding-top: 150px;
    margin: 0 0 20px !important;
    max-width: 100%;
    width: 100%;

    &::after {
      top: -40px;
      left: auto;
      width: 100%;
      background-size: 225px auto;
      background-position: top center;
    }

    &:nth-of-type(even)::after {
      right: auto;
    }
  }

  .title-form {
    font-size: 28px;
  }

  .form__fh > * {
    width: 100%;
  }

  .form-vacancies .form__file-label {
    margin-bottom: 90px;
  }

  .form-vacancies .lze-capcha {
    margin-top: -122px;
  }
}
