.team .wrapper {
  padding-top: 130px;
  margin-bottom: 145px;
  background: #fff url('../img/about-top.png') no-repeat top center/100% auto;
}

.team__text {
  margin-bottom: 100px;
  font-weight: 500;
  max-width: 600px;

  & .accent {
    font-weight: 700;
    text-decoration: underline;
  }
}

.team__work .wrapper {
  margin-bottom: 155px;
  background: url("../img/hands.png") no-repeat right 15% top 50px,
              url("../img/wark.png") no-repeat top center/100% auto;
}

.team__list {
  padding-left: 0;
  margin-bottom: 150px;
  font-weight: 500;
  max-width: 690px;

  & .accent {
    color: $prime-color;
    text-decoration: underline;
  }
}

.team__people .wrapper {
  padding-bottom: 470px;
  background: url("../img/team-bg.png") no-repeat bottom center/100% auto,
              url("../img/team-icon.png") no-repeat top center/100% auto;
}

.team__people .page__subtitle {
  margin-bottom: 50px;
}

.team__people-text {
  max-width: 692px;
  font-weight: 500;
  margin-bottom: 70px;
}

.team__people-list {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.team__people-item {
  position: relative;
  margin-right: 10px;
  margin-bottom: 10px;
  width: 245px;
  background: linear-gradient(38.92deg, rgba(0, 0, 0, 0.8) 28.04%, rgba(0, 0, 0, 0) 62.91%);

  & .item-wrapper {
    position: absolute;
    left: 20px;
    bottom: 20px;
    color: #fff;
  }

  .name {
    font-family: $title-font;
    font-size: 22px;
    margin-bottom: 20px;
  }

  .title {
    font-weight: 500;
    font-size: 12px;
    margin-bottom: 20px;
    opacity: 0.7;
  }

  .value {
    font-weight: 500;
    text-transform: uppercase;
    min-height: 36px;
    margin-bottom: 0;
  }
}

@media (max-width: $mobile-width) {
  .team .wrapper {
    padding-top: 100px;
    margin-bottom: 30px;
  }

  .team h1 {
    margin-bottom: 30px;
  }

  .team__text {
    margin-bottom: 20px;
  }

  .team__list {
    margin-bottom: 50px;
  }

  .team__work .wrapper {
    padding-top: 45px;
    background-size: 40px auto, 100% auto;
    margin-bottom: 30px;
  }

  .team__people .wrapper {
    padding-top: 50px;
    background-size: 38px auto, 100% auto;
    padding-bottom: 30px;
  }

  .team__people-text {
    margin-bottom: 30px;
  }

  .team__people-item {
    width: 140px;
    text-align: center;

    & .title {
      display: none;
    }

    & .title {
      margin-bottom: 15px;
    }
  }

  .team__people-item .item-wrapper {
    width: 100%;
    left: 0;
  }
}

@media (max-width: 490px) {
  .team__people-item {
    width: 49%;
    margin-right: 2%;

    &:nth-of-type(even) {
      margin-right: 0;
    }
  }
}
